/*RESET*/
html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  font-family: "Arial", sans-serif;
  background-color: #1c1c1c;
}

.page {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: clamp(1.25rem, 3vw, 3rem);
  padding-inline: clamp(1.56rem, 6.25vw, 6.25rem);
  min-height: 100vh;
}

@media screen and (max-width: 800px) {
  .page {
    flex-flow: column nowrap;
    gap: clamp(3rem, 6vw, 6rem);
    padding-inline: 0;
  }
}
.card {
  flex: 1 1;
  border-radius: 1rem;
  padding: 0 1.4em 1.4em 1.4em;
  font-size: clamp(0.75rem, 2.9vw, 3rem);
  background-color: white;
  color: black;
}

.card__title {
  position: relative;
  top: -35px;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 0.5em 1.3em;
  font-size: clamp(1.2rem, 4.3vw, 4.5rem);
  font-weight: 900;
  background-color: #f50186;
  text-align: center;
}

.card__content {
  font-size: clamp(0.6rem, 2.2vw, 2.3rem);
  line-height: 1.5;
}

@media screen and (max-width: 800px) {
  .card {
    flex: 0;
  }
}
.pointer {
  flex: 1 1;
  order: 1;
  font-family: "Love Ya Like A Sister", cursive;
  font-size: clamp(2rem, 6vw, 6rem);
  color: #27c1e4;
}

@media screen and (max-width: 800px) {
  .pointer {
    flex-grow: 0;
    order: 0;
    /*margin-bottom: 6rem;*/
  }
}
.nowrap {
  white-space: nowrap;
}